@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
    margin: 0;
    font-family: 'Noto Sans KR', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: 'Noto Sans KR', sans-serif;
  }
  ul,li{
    padding:0px;
    margin:0px;
  }
  li{
    list-style:none;
  }
  a{
    text-decoration: none;
  }
.header{
    width: 100%;
    position: fixed;
    /* background-color: rgb(242, 170, 170); */
    background-color: white;
    top:0;
    z-index: 110;
    background-color: #59aaeb;
}
.top{
    width:90%;
    margin:0 auto;
}
.top .logo a{
    font-size:30px;
    font-weight: 600;
    display: inline-block;
}
.top .logo{
    display: inline-block;
}
.top .navigation{
    float:right;
}
.navigation > .pc_navigation ul li{
    float:left;
    font-weight: 600;
    margin-top: 2px;
    padding:15px 0px;
    transition: transform .3s;
}
.navigation > .pc_navigation ul li:hover{
    transform: scale(1.3);
}

.navigation > .pc_navigation ul li a{
    color: black;
    padding:15px;
}

@media (max-width: 768px) {
    .navigation > .pc_navigation{
        display: none;
    }
}
.footer{
    width:100%;
    height:50px;
    margin-top:50px;
    border-top:1px solid #e3e3e3;
}
.pc_footer{
    width:80%;
    margin:0 auto;
    padding:30px 0px 50px 0px;
}
.copyright{
    float:left;
}
.sitemap{
    float:left;
    margin-left:50px;
}
.sitemap ul li{
    float: left;
    padding-left:10px;
}
.SNS{
    float:right;
    margin-right:50px;
}
.SNS ul li{
    padding-left:20px;
    float:left;
}
.mob_menu{
    display: none;
    /* background-color: rgb(242, 170, 170); */
    background-color:#59aaeb;
    width:100%;
    height:50px;
    position: fixed;
    margin-top: 50px;
    bottom: 0;
box-shadow: 3px 3px 3px 0px rgba(166,228,255,1);
}
.mob_menu ul{
    display: flex;
}
.mob_menu ul li{
    float:left;
    /* color:black; */
    font-weight: 600;
    flex:1 1;
    text-align: center;
}
.mob_menu ul li a{
    color:black;
    display: inline-block;
    padding: 17px 0px;
}

@media (max-width: 768px) {
    .footer{
        margin-top: 0px;
    }
    .pc_footer{
        display: none;
    }
    .mob_menu{
        display: block;
    }
    .mobile_navi_click{
        color:red !important;
    }
}

.poster1{
    background-color: rgb(185,234,255);
    width:100%;
    min-height: 250px;
}
.poster1_on{
    transition:7s;
    opacity: 1;
}
.poster_img{
    min-height: 250px;
    background-color: rgb(88,204,255);
}
/* 포스터 2 animation */

.poster_img_on{
    transition: 10s;
    opacity: 1;
}

.none{
    transition: 2.5s;
    zoom: 0.8;
    font-weight: 600;
    opacity: 0;
}
.animate_wrap{
    margin: 0 auto;
    width: 80%;
    text-align: center;
    position:absolute;
    bottom: 55%;
    left: 10%;
}
/* 포스터 1 텍스트 animation */
.animate_text{
    transition:3.5s;
    color:rgb(189,91,97);
    font-size:46px;
    margin:0;
}
.animate_text1{
    font-weight: 600;
    transition: 4s;
    opacity: 1;
    color:rgb(189,91,97);
    font-size:52px;
    margin:0;
}

@media (max-width: 1400px) {
    .animate_wrap{
        bottom: 60%;
    }
}

@media (max-width: 1260px) {
    .animate_wrap{
        bottom: 63%;
    }
}

@media (max-width: 1058px) {
    .animate_wrap{
        bottom: 69%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:36px;
    }
    .animate_text1{
        font-size:42px;
    }
}

@media (max-width: 798px) {
    .animate_wrap{
        bottom: 76%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:26px;
    }
    .animate_text1{
        font-size:32px;
    }
}

@media (max-width: 580px) {
    .animate_wrap{
        bottom: 78%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:22px;
    }
    .animate_text1{
        font-size:28px;
    }
}

@media (max-width: 480px) {
    .animate_wrap{
        bottom: 77.5%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:17px;
    }
    .animate_text1{
        font-size:23px;
    }
}
